.Dark {
  background-color: #E0BB00;
  color: #001219;
}

.Light {
  background-color: #BA181B;
  color: #fff;
}

.Btn {
  font-family: inherit;
  font-weight: 400;
  border-radius: 5px;
  border: none;
  outline: none;
  margin: 1.5rem 0;
  padding: 0.5rem 1rem;
  letter-spacing: 2px;
  cursor: pointer;
  transition: background-color 1s, color 0.5s, letter-spacing 0.2s;
}

.Btn:hover {
  letter-spacing: 3px;
}